import React from "react"
import { css } from "@emotion/core"
import { useStaticQuery, Link, graphql } from "gatsby"

import { rhythm } from "../utils/typography"
export default function Layout({ children }) {
  const { title } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  ).site.siteMetadata

  return (
    <div>
      <div
        css={css`
          margin: 0 auto;
          max-width: 1000px;
          padding: ${rhythm(2)};
          padding-top: ${rhythm(1.5)};
        `}
      >
        <Link to={`/`}>
          <h3
            css={css`
              margin-bottom: ${rhythm(2)};
              display: inline-block;
              font-style: normal;
              font-family: Nunito;
            `}
          >
            {title}
          </h3>
        </Link>

        <Link
          to={`/Background/`}
          css={css`
            float: right;
            margin-left: 20px;
          `}
        >
          AboutMe
        </Link>
        <Link
          to={`/Projects/`}
          css={css`
            float: right;
          `}
        >
          Projects
        </Link>
      </div>
      {children}
    </div>
  )
}
